

*{
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    color: aliceblue;
}
.container{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}


.main-container{
    padding: 40px;
    width: 60%;
    border-radius: 20px;
    background-image: url("https://rare-gallery.com/thumbs/124416-miui-8-rainy-weather-background-minimal-hd.png");
    background-size: 100% 100%;
}


.head h1{
    font-size: 4vw;
}

.search-container{
    display: flex;
    align-items: center;  
    justify-content: center;
    margin: 20px auto;
    border-radius: 5px;
}

.search-bar{
    width: 50%;
    margin: 2vw;
    border-radius: 5px;
    outline: none;
    border: none;
    padding: 10px 10px;
    color: black;
    font-size: 1.2vw;
}

.search-bar:focus{
    outline: 1px solid red;
    box-shadow: 0px 0px 10px blue;
}

.icons{
    border: 1px solid;
    padding: 1vw;
    border-radius: 50%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 1vw;
    cursor: pointer;
}

.icons:hover{
    background-color: rgb(219, 219, 219);
}

.fa-magnifying-glass{
    color: black;
}

.temp{
    margin: auto;
    margin-top: 1px;
   
}

.temp h1{
    font-size: 7vw;
}

.info{
    margin: auto;
    margin-top: 1vw;
    display: flex;
    align-items: center;
    justify-content: space-around;
}


@media screen and (max-width: 600px) {
    .info{
        flex-direction: column;
        align-items: flex-start;
    }

    .temp{
        margin: 6vw;
    }
    .search-bar{
        width: 80%;
    }

    .head h1{
        font-size: 7vw;
    }

    .search-bar{
        font-size: 12px;
        padding: 3px 8px;
    }
}